.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;

    &__bg {
        background: #000000;
        opacity: 0.7;
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &--active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #FFFFFF;
        width: 40em;
        max-width: calc(100% - 1em);
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        padding: 6.125em 3.125em;
        position: relative;
        z-index: 3;
    }
    &__close{
        position: absolute;
        top: 1.25em;
        right: 1.25em;
        width: 1.125em;
        display: block;
        img{
            width: 100%;
            display: block;
        }
    }
    &__top{
        text-align: center;
        @include grid100gap(.625em);
    }
    .consult__form{
        grid-template-columns: 100%;
        margin-top: 1.25em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            padding: 2em 1em;
        }
    }
}
