.space{
  padding: .625em 0 3.125em;
  &__top{
    @include grid100gap(.625em);
  }
  .video{
    margin-top: 3.125em;
    &__play{
      top: 3.125em;
      transform: translateX(-50%);
    }
  }
}

.space-info{
  padding: 3.125em 0;
  overflow: hidden;
  &__wrap{
    padding-bottom: 7.6875em;
  }
  &__grid{
    padding: 6.25em 3.125em;
    background: #F8F8F8;
    min-height: 39.375em;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-top: 1.875em solid #fff;
      border-right: 1.875em solid transparent;
      top: 0;
      left: 0;
    }
  }
  &__content{
    max-width: 32em;
  }
  .catalog-card__img{
    &:after {
      right: unset;
      left: -1px;
      border-left: unset;
      border-top: 1.875em solid #F8F8F8;
      border-right: 1.875em solid transparent;
    }
  }
  @media screen and (min-width: 993px) {
    .space-slider {
      position: absolute;
      right: 3.125em;
      top: 10.75em;
      width: 50.3125em;
    }
    .catalog-card__img {
      height: 36.25em;
    }
    .swiper-nav {
      left: 1.875em;
      bottom: 1.875em;
      right: unset;
    }
  }
}

.plan{
  margin: 3.125em 0;
  padding: 6.25em 0;
  background-color: $gray;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }
  &__img{
    @include center;
    background-color: #fff;
    min-height: 100%;
    &:after{
      right: unset;
      left: 0;
      border-top: 5.75em solid #e0e3e4;
      border-left: none;
      border-right: 5.75em solid transparent;
    }
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__props{
    @include grid100gap(.3125em);
  }
}

.prop-card{
  display: flex;
  align-items: center;
  padding: .5em 1em;
  min-height: 3.125em;
  background-color: #fff;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-top: .625em solid $gray;
    border-right: .625em solid transparent;
    top: 0;
    left: 0;
  }
  &__icon{
    width: 1.875em;
    height: 1.875em;
    flex-shrink: 0;
    margin-right: 1.25em;
    @include center;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__title,
  &__desc{
    font-size: 1.125em;
    line-height: 140%;
    font-family: $font2;
    text-transform: uppercase;
  }
  &__desc{
    padding-left: 1em;
    margin-left: auto;
  }
}

.gallery{
  padding: 3.125em 0;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    max-width: 5.25em;
  }
  &__tabs{
    min-width: 26.25em;
  }
  &__tab{
    display: flex;
    align-items: center;
    padding: 0 1.875em;
    height: 3.75em;
    text-transform: uppercase;
    font-family: $font2;
    color: #CBCBCB;
    transition: all .3s;
    span{
      font-size: 1.625em;
    }
    &:hover{
      color: $primary;
      cursor: pointer;
    }
    &._active{
      background-color: $primary;
      color: #fff;
      pointer-events: none;
    }
  }
  &__content{
    margin-top: 3.125em;
  }
  &__img{
    @include imgCover(36.45%);
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.gallery-slider{
  position: relative;
  .swiper{
    &-pagination{
      bottom: 1.25em;
      left: 16.5625em;
      z-index: 3;
      width: unset;
      color: transparent;
      font-family: $font2;
      font-weight: 700;
      display: flex;
      &-total,
      &-current{
        color: #fff;
        line-height: 140%;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
      &-current{
        font-size: 6.25em;
        margin-right: .8em;
        position: relative;
        &:after{
          content: '';
          position: absolute;
          top: .35em;
          left: 100%;
          margin-left: .15em;
          width: .5em;
          height: .05em;
          background-color: $primary;
        }
      }
      &-total{
        font-size: 1.875em;
        padding-top: .533em;
      }
    }
    &-button{
      &-prev{
        left: 6.25em;
      }
      &-next{
        right: 6.25em;
      }
    }
  }
}

.tech{
  &__tabs{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(2,50%);
  }
  &__tab{
    border: 2px solid #E4E4E4;
    border-radius: 31.25em;
    height: 3.75em;
    text-transform: uppercase;
    font-family: $font2;
    color: #C5C5C5;
    @include center;
    transition: all .3s;
    cursor: pointer;
    span{
      font-size: 1.625em;
      line-height: 140%;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      background-color: $primary;
      border-color: $primary;
      color: #fff;
    }
  }
  &__content{
    margin-top: .625em;
  }
  &__items{
    @include grid100gap(.625em);
  }
}

.tech-card{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .3125em));
  gap: .625em;
  align-items: center;
  background: #F8F8F8;
  padding: .9375em 1.25em;
  &__title{
    font-size: 1.375em;
    line-height: 140%;
    font-family: $font2;
    text-transform: uppercase;
  }
  &__desc{
    font-size: 1.375em;
    line-height: 150%;
    color: $dark;
  }
}

.consult-wrap{
  background-image: url(../img/consult.webp);
  padding-bottom: 7.25em;
  background-size: cover;
  background-position: center;
  margin-top: 6.875em;
  .consult{
    margin-top: -4.25em;
    position: relative;
    z-index: 2;
    &__inner{
      border: 1px solid #C5C5C5;
    }
  }
}


@media screen and (max-width: 992px){
  .space{
    overflow: hidden;
    padding-bottom: 1em;
    .text46{
      font-size: 1.25em;
    }
    .video{
      margin-top: 1.5em;
      &__play{
        top: 50%;
        transform: translate(-50%,-50%);
      }
    }
  }

  .space-info{
    padding: 1em 0;
    &__wrap{
      padding: 0;
    }
    &__grid{
      min-height: unset;
      padding: 0;
      background-color: transparent;
      &:after{
        display: none;
      }
    }
    .space-slider{
      margin-top: 1em;
    }
    .catalog-card__img{
      &:after{
        border-top: 1.875em solid #FFF;
      }
    }
  }

  .plan{
    margin: 1em 0;
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1em;
    }
    &__img{
      min-height: unset;
      &:after{
        border-top: 1.875em solid #e0e3e4;
        border-right: 1.875em solid transparent;
      }
    }
  }

  .prop-card{
    @include grid100gap(.25em);
    padding: .75em;
    &__title,
    &__desc{
      font-size: .875em;
      margin: 0;
      padding: 0;
    }
  }

  .gallery{
    padding: 1em 0;
    &__top{
      display: block;
    }
    &__tabs{
      width: 25em;
      min-width: unset;
      max-width: 100%;
      margin-top: 1em;
    }
    &__content{
      margin-top: 1em;
    }
    &__img{
      padding-bottom: 60%;
    }
  }

  .gallery-slider{
    .swiper{
      &-container{
        max-width: 95%;
      }
      &-slide{
        padding-right: .625em;
      }
      &-pagination,
      &-button{
        display: none;
      }
    }
  }

  .tech{
    &__tabs{
      margin-top: 1em;
    }
    &__tab{
      height: 3em;
      span{
        font-size: 1em;
      }
    }
  }

  .tech-card{
    &__title,
    &__desc{
      font-size: .875em;
    }
  }

  .consult-wrap{
    margin-top: 2em;
    padding: 0;
    background: transparent;
    .consult{
      margin: 0;
      &__inner{
        border: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .space{
    &__img{
      img{
        height: 16em;
        width: auto;
        margin-left: -2em;
      }
    }
  }

  .gallery{
    &__tab{
      padding: 0 1em;
      height: 2.5em;
      span{
        font-size: 1.125em;
      }
    }
  }

  .tech{
    &__tab{
      text-align: center;
      span{
        font-size: .75em;
      }
    }
  }

  .tech-card{
    grid-template-columns: 100%;
    padding: .75em 1em;
    gap: .25em;
    &__title,
    &__desc{
      font-size: .75em;
    }
  }
}
