.news {
  padding: 0.625em 0 6.25em;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 0.625em));
    gap: 0.625em;
    grid-auto-flow: dense;
    margin-top: 1.875em;
  }
  &__btn {
    width: 24.6875em;
    margin: 1.875em auto 0;
    &:after {
      background-image: url("../img/update.svg");
    }
  }
}

.news-card {
  border: 1px solid #F2F2F2;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-top: 1.875em solid #fff;
    border-right: 1.875em solid transparent;
    top: -2px;
    left: -2px;
  }
  @media screen and (min-width: 993px) {
    &._big{
      grid-column: span 2;
    }
  }
  &__img {
    height: 21.875em;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    margin-top: 1.25em;
    padding: 0 1.875em 1.875em;
  }
  &__title {
    font-family: 'Rubik',sans-serif;
    font-size: 1.75em;
    line-height: 140%;
    text-transform: uppercase;
    @include truncate(2);
  }
  &__text {
    margin-top: 0.625em;
    p {
      font-size: 1.125em;
      line-height: 150%;
      color: #383838;
      @include truncate(3);
    }
  }
}

.news2{
  padding: 1.5em 0 6.25em;
  .news__cards{
    margin-top: 2.5em;
  }
}

.news-item{
  padding: 0.625em 0 3.125em;
  &__text{
    margin-top: 3.125em;
    max-width: 80.5em;
  }
  .video{
    margin-top: 3.125em;
  }
}


@media screen and (max-width: 992px) {
  .news {
    padding: 0.625em 0 2em;
    &__cards {
      grid-template-columns: repeat(2,calc(50% - 0.625em));
    }
  }

  .news-card {
    &__img {
      height: 12.5em;
    }
    &__desc {
      padding:0 1.25em 1.25em;
    }
    &__title {
      font-size: 1.5em;
    }
    &__text {
      p {
        font-size: .875em;
      }
    }
  }

  .news2{
    padding: 1em 0 2em;
    .news__cards{
      margin-top: 1.25em;
    }
  }

  .news-item{
    padding: 0.625em 0 1em;
    &__text{
      margin-top: 1.25em;
    }
    .video{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      margin-top: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .news {
    &__cards {
      grid-template-columns: repeat(1,calc(100% - 0.625em));
    }
    &__btn {
      width: 100%;
    }
  }

  .news-card {
    &__desc {
      padding:0 0.9375em 0.9375em;
    }
  }
}
