.contacts{
  padding: .625em 0 3.125em;
  flex-grow: 1;
  &__grid{
    margin-top: 1.875em;
  }
  &__items,
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }
  .map{
    width: calc(100% + 6.25em);
    height: 19em;
    min-height: 100%;
    margin-left: -6.25em;
  }
  &__items{

  }
}

.contact-card{
  //min-height: 30em;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 577px){
    &:nth-child(4){
      ._line{
        transform: translate(-.4em,1em) rotate(-45deg);
      }
      ._line,
      &:before,
      &:after{
        right: unset;
        left: -2px;
      }
    }
    &:nth-child(1),
    &:nth-child(2){
      &:after{
        top: unset;
        bottom: 0;
      }
      ._line,
      &:before{
        top: unset;
        bottom: -1px;
      }
    }
    &:nth-child(1){
      ._line{
        transform: translate(.4em,-1em) rotate(-45deg);
      }
    }
    &:nth-child(2){
      &:before{
        right: unset;
        left: 0;
      }
      ._line,
      &:after{
        right: unset;
        left: -2px;
      }
      ._line{
        transform: translate(-.4em,-1em) rotate(45deg);
      }
    }
  }
  &._top{
    .contact-card__content{
      margin: 0;
    }
  }
  &__content{
    margin-top: auto;
    padding-top: 2em;
    @include grid100gap(.625em);
  }
  &__title{
    font-family: $font2;
    font-size: .875em;
    line-height: 140%;
    text-transform: uppercase;
    color: #838C8F;
  }
  &__link{
    text-transform: unset;
    font-family: $font1;
    font-feature-settings: 'pnum' on, 'lnum' on;
  }
  &__site{
    display: flex;
    align-items: center;
    margin-top: -.3125em;
    color: $primary;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .9375em;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-bottom: 1em;
    &__grid{
      grid-template-columns: 100%;
    }
    .map{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      min-height: unset;
      height: 20em;
    }
  }

  .contact-card{
    min-height: unset;
    &__content{
      padding-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
